import React from 'react'
import { Form, Button, Input, Card } from 'antd'
import { AdminLogin } from '../Api/CoreApi'

function Admin_Login() {
  const submit = async(i) => {
    const response = await AdminLogin(i)
    localStorage.setItem('Admin', response.Admin) // Store actual username

    console.log(i, '******* i ********')
  }

  return (
    <div style={{height:'748px',width:'100%',paddingTop:'10%'}}>
      <Card style={{ width: '40%', marginLeft:'35%',boxShadow: "2px 2px 10px rgba(0, 0, 0, 0.2)" }}>
        <Form onFinish={submit}>
          <Form.Item label="Username" name="username" rules={[{ required: true, message: 'Please enter your username!' }]}>
            <Input style={{ width: '90%' }} />
          </Form.Item>

          <Form.Item label="Password" name="password" rules={[{ required: true, message: 'Please enter your password!' }]}>
            <Input type="password" style={{ width: '90%' }} />
          </Form.Item>

          <Form.Item>
            <Button type="primary" htmlType="submit" style={{ display: 'block', margin: '0 auto' }}>
              Submit
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </div>
  )
}

export default Admin_Login
