import React from 'react'
import {Form, Button, Input, Card} from 'antd'
import { AdminPost } from '../Api/CoreApi'
function Admin_Reg() {
    const submit = async(i)=>{
      const response = await AdminPost(i)
      // const response = await AdminPost(i)
      console.log(i,'******* i ********')
    }
  return (
    <div style={{paddingTop:'100px'}}>
        <Card style={{width:'40%',marginLeft:'30%',paddingLeft:'1%',boxShadow: "2px 2px 10px rgba(0, 0, 0, 0.2)"}}>
        <Form onFinish={submit}>
            <Form.Item label='Username' name='username'>
                <Input style={{width:'90%'}}/>
            </Form.Item>

            <Form.Item label='Password' name='password'>
                <Input style={{width:'90%'}}/>
            </Form.Item>
            <Form.Item>
                <Button htmlType='sumit' style={{marginLeft:'40%'}}>Submit</Button>
            </Form.Item>
        </Form>
        </Card>
    </div>
  )
}

export default Admin_Reg