import React, { useEffect, useState } from 'react'
import { Button } from 'antd'
import { BookGet,BookRecordPost } from './Api/CoreApi'
import { Link } from 'react-router-dom'
function HomePage() {
    const Userid = localStorage.getItem('User')
    const int_Userid = (parseInt(Userid))
    const baseurl = 'http://127.0.0.1:8000/'
    const [book, setbook] = useState([])

    const get = async () => {
        const response = await BookGet()
        setbook(response)
    }

    useEffect(() => {
        get()
    }, [])

    const apply = async (i)=>{
        const data = ({apply : int_Userid,book : i.book})
        delete data.img;
        console.log(data,'******* i ********')
        const response = await BookRecordPost(data)
    }
    return (
        <div>
            <div>
                {/* <div style={{display:'flex',height:'50px',columnGap:'20px',backgroundColor:'rgba(7, 110, 148,1)'}}>
                <Link to='/Admin_Reg'><p style={{marginLeft:'1300px',color:'white'}}>Register</p></Link>
                <Link to='/Admin_Reg'><p style={{marginLeft:'85%',color:'white'}}>Login</p></Link></div> */}
                <div>
                    <p ><img style={{ width: '100%', height: '500px' }} src='https://www.skoolbeep.com/blog/wp-content/uploads/2020/12/WHAT-IS-THE-PURPOSE-OF-A-LIBRARY-MANAGEMENT-SYSTEM-min.png' /></p>
                    {int_Userid === 0 && (
                        <>
                            <p style={{ textAlign: 'center', marginTop: '-50px' }}><Link to='/User_Login'><Button style={{ width: '200px', fontSize: '18px' }}>Join Us</Button></Link></p>
                        </>
                    )}
                </div>
                {int_Userid > 0 && (
                    <div style={{ display: 'flex', columnGap: '20px', marginLeft: '25%' }}>
                        {book.map(i => (
                            <div style={{ width: '250px', height: '300px', borderRadius: '10px', boxShadow: "2px 2px 10px rgba(0, 0, 0, 0.2)" }}>
                                <div>
                                    <img style={{ width: '250px', marginTop: '5px' }} src={`${baseurl}${i.img}`} />
                                    <p style={{ textAlign: 'center', fontSize: '20px' }}>{i.book}</p></div>
                                <p style={{ textAlign: 'center' }}><Button onClick={()=>apply(i)}>Apply</Button></p>
                            </div>
                        ))}
                    </div>
                )}

                {/* <div style={{border:'2px solid',height:'30px',marginTop:'100px'}}>Copyright c 2025</div> */}
            </div><br /><br /><br />
        </div>
    )
}

export default HomePage



