import React from 'react'
import { Link } from 'react-router-dom'
import { useNavigate } from 'react-router-dom'
function Header() {
    const Navigate = useNavigate()
    const Userid = localStorage.getItem('User')
    const int_Userid = (parseInt(Userid))
    const logout = ()=>{
     localStorage.setItem('User',0)
     Navigate('/')
     window.location.reload();

    }
    return (
        <div>
            <div style={{ display: 'flex', height: '50px', columnGap: '20px', backgroundColor: 'rgba(7, 110, 148,1)' }}>
                {int_Userid == 0 && (
                    <>
                <Link to='/User_Reg'><p style={{ marginLeft: '1300px', color: 'white' }}>Register</p></Link>
                <Link to='/User_Login'><p style={{ marginLeft: '30px', color: 'white' }}>Login</p></Link>
                </>
                )}
                
                <p onClick={logout} style={{ marginLeft: '1300px', color: 'white' }}>Logout</p></div>
            <div>
            <p style={{textAlign:'center',fontSize:'28px',color:'white',marginTop:'-45px'}}>Library mnagement System </p>

            </div>
        </div>
    )
}

export default Header